.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin-inline: 3px;
  display: flex;
}

.card__title {
  margin-top: 40px;
  margin-bottom: 4px;
  font-weight: 500;
  line-height: 18px;
  transition: transform .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 10px;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  padding: 12px 68.5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.page {
  --column-count: 1;
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.page__body {
  margin: 0;
}

.page__main {
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .page__main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .page__main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .page__main {
    --column-count: 4;
  }
}

.p {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  text-align: center;
  text-transform: uppercase;
  display: inline-block;
}

.nav__link {
  color: #060b35;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: transform .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}

